import {changeRequest} from "../../../store/slices/stepSlice";
import {useDispatch, useSelector} from "react-redux";
import {changeOrderValues, orderValueSelector} from "../../../store/slices/OrderDetailsSlice";
import {toast} from "react-hot-toast";

const SubCustomerNameAndPhoneStep6 = () => {
    const dispatch = useDispatch();
    const {customerName, customerPhone, customerEmail} = useSelector(orderValueSelector)

    const nextPageHandler = () => {
        if(customerEmail && customerName && customerPhone){
            dispatch(changeRequest({
                change: true,
                step: 7,
                className: 'rpage'
            }))
        }else{
            toast.error('Please fill all fields!')
        }

    }

    const changeValue = e => {
        dispatch(changeOrderValues({name: e.target.name, value: e.target.value}))
    }



    return (
        <div className="md:pt-36 pt-36">
            <div className="mx-5 md:w-1/2 md:mx-auto mb-16 px-5">

                <div className="py-2">
                    <p className="font-bold font-open-sans text-gray-600 pb-4">What is your name?</p>
                    <input
                        value={customerName}
                        name={'customerName'}
                        onChange={changeValue}
                        type="text"
                        className="focus:outline-none border border-gray-600 shadow w-full rounded px-2 py-2 font-open-sans text-gray-600"
                        placeholder={'Enter your name here'}

                    />
                </div>


                <div className="py-2 mt-2">
                    <p className="font-bold font-open-sans text-gray-600">What is your phone number?</p>
                    <p className="font-light font-open-sans text-xs text-gray-500 pb-2">No spam, For order purpose
                        only!</p>
                    <input
                        name={'customerPhone'}
                        value={customerPhone}
                        onChange={changeValue}
                        type="text"
                        className="focus:outline-none border border-gray-600 shadow w-full rounded px-2 py-2 font-open-sans text-gray-600"
                        placeholder={'Enter your phone number here'}

                    />
                </div>

                <div className="py-2 mt-2">
                    <p className="font-bold font-open-sans text-gray-600">What is your Email Address?</p>
                    <p className="font-light font-open-sans text-xs text-gray-500 pb-2">No spam, For order purpose
                        only!</p>
                    <input
                        type="text"
                        value={customerEmail}
                        name={'customerEmail'}
                        onChange={changeValue}
                        className="focus:outline-none border border-gray-600 shadow w-full rounded px-2 py-2 font-open-sans text-gray-600"
                        placeholder={'Enter your email address'}

                    />
                </div>


                <div className={'mt-2  flex justify-between'}>
                    <button
                        onClick={() => dispatch(changeRequest({
                            change: true,
                            step: 5,
                            className: 'page'
                        }))}
                        className="px-3 py-1 text-sm font-bold rounded shadow bg-gray-600 text-white uppercase font-open-sans focus:outline-none">
                        Back
                    </button>

                    <button
                        onClick={nextPageHandler}
                        className="px-3 py-1 text-sm font-bold rounded shadow bg-indigo-600 text-white uppercase font-open-sans focus:outline-none">
                        Next
                    </button>

                </div>


            </div>
        </div>
    );
}

export default SubCustomerNameAndPhoneStep6
