import React, { useState } from "react";
import {CSSTransition} from "react-transition-group";
import {useSelector, useDispatch} from "react-redux";
import {closeCartModal, cartModalSelector } from '../../../store/slices/utilsSlice';
import {shopContentsSelector, incPreCartQtyAction, decPreCartQtyAction, addToCartAction} from "../../../store/slices/shopContentsSlice";
import {FaMinus, FaPlus, FaTimes} from "react-icons/fa";
import cloudinaryUrlGenerator from "../../../helpers/cloudinaryUrlGenerator";
import { toast } from "react-hot-toast";

const AddToCartModal = () => {


    const modalData = useSelector(cartModalSelector);
    const { preCart } = useSelector(shopContentsSelector)
    const dispatch = useDispatch();
    const [innerM, setInnerM] = useState(false);
    const closeModalFromWrapper = (e) => {
        e.target.dataset.wrapper && setInnerM(false)
    }

    const incrementQty = () => {
        if(preCart.qty < preCart.stock){
            dispatch(incPreCartQtyAction())
        }
    }

    const decrementQty = () => {
        if(preCart.qty > 1){
            dispatch(decPreCartQtyAction())
        }
    }

    const addToCart = () => {
        dispatch(addToCartAction(preCart))
        dispatch(closeCartModal())
        toast.success('Product added to your cart!')
    }


    return (
        <CSSTransition
            in={modalData.open}
            classNames={'fade'}
            timeout={300}
            onEntered={() => setInnerM(true)}
            unmountOnExit
        >
            <div>
                <div data-wrapper={true} onClick={closeModalFromWrapper}
                     style={{zIndex: 9999}}
                     className="fixed -top-0 -left-0 h-screen w-screen bg-gray-800 bg-opacity-80 flex justify-center items-center">

                    <CSSTransition
                    in={innerM}
                    classNames={'alert'}
                    timeout={300}
                    onExited={() => dispatch(closeCartModal())}
                    unmountOnExit
                    >
                        {
                            preCart?.name && (
                                <div>
                                    <div className="rounded-xl bg-white shadow-lg overflow-hidden relative">
                                        <FaTimes onClick={() => setInnerM(false)} size={18} className={'absolute right-3 top-3'} />
                                        <div className="w-80 md:w-72 h-80 md:h-72">

                                            <img src={cloudinaryUrlGenerator(preCart?.product_images[0]?.source)} alt="food" className="w-full h-full hover:object-cover object-contain transition-all duration-300 object-center"/>
                                            <span
                                                className="absolute text-sm -right-0 bg-yellow-500 font-open-sans font-bold tracking-wider px-2 top-48">{parseFloat(preCart?.price)}$</span>
                                        </div>

                                        <h2 className={'w-80 md:w-72 py-2 font-bold font-open-sans text-center text-xl'}>{preCart?.name}</h2>

                                        <p className="w-80 md:w-72 px-2 py-2 text-sm font-open-sans text-gray-700 text-center">
                                            {preCart?.description || 'No description available!'}
                                        </p>

                                        <div className="w-80 md:w-72 px-12 py-2">
                                            <div className="w-full py-2 flex items-center justify-between border px-2 rounded">
                                                <div onClick={decrementQty} className="h-7 w-7 overflow-hidden cursor-pointer rounded-full bg-gray-600 flex justify-center items-center">
                                                    <FaMinus  className={'inline'}  color={'white'}/>
                                                </div>

                                                <span className={'font-open-sans text-gray-700 font-bold text-lg'}>{preCart?.qty}</span>

                                                <div onClick={incrementQty} className="h-7 w-7 overflow-hidden cursor-pointer rounded-full bg-gray-600 flex justify-center items-center">
                                                    <FaPlus  className={'inline'}  color={'white'}/>
                                                </div>
                                            </div>
                                        </div>
                                        <div className="w-80 md:w-72 pt-2 pb-5 text-center">
                                            <p className="text-center py-2">
                                                <button
                                                    onClick={addToCart}
                                                    className="py-1 px-3 rounded shadow font-open-sans text-lg uppercase text-white bg-yellow-500 focus:outline-none hover:shadow-xl transition-all duration-300 hover:bg-yellow-600">
                                                    ADD TO CART
                                                </button>
                                            </p>
                                        </div>



                                    </div>
                                </div>
                            )
                        }

                    </CSSTransition>

                </div>
            </div>




        </CSSTransition>


    )
}


export default AddToCartModal
