import {useState} from "react";
import { useDispatch, useSelector } from "react-redux";
import { shopContentsSelector, selectCategoryAction } from "../../../store/slices/shopContentsSlice";
import { changeRequest } from "../../../store/slices/stepSlice";
import cloudinaryUrlGenerator from "../../../helpers/cloudinaryUrlGenerator";

const SubCategorySelectorStep3 = () => {
    const dispatch = useDispatch();

    const shopContents = useSelector(shopContentsSelector)

    const [more, setMore] = useState(false);

    const categories = shopContents.uncategorized.length ? [...shopContents.categories, {name: 'UNCATEGORIESED', image: '', products: shopContents.uncategorized}] : shopContents.categories;


    const selectCategoryHandler = (category) => {
        dispatch(selectCategoryAction(category.products))
        dispatch(changeRequest({
            change: true,
            step: 4,
            className: 'rpage'
        }))

    }


    return (
        <div className="md:pt-36 pt-36">
            <div className="mx-5 md:w-2/3 md:mx-auto mb-16">
                <div className="inline">
                    <h2 className="font-open-sans text-lg text-center md:text-left font-bold">
                        Please choose from our categories
                    </h2>
                    <p className="text-xs py-2 font-open-sans text-gray-800 text-center md:text-left">Showing available
                        categories.</p>

                    <div
                        className={`flex flex-wrap ${more ? 'justify-evenly' : 'md:justify-start justify-evenly'} gap-4 mt-5`}>
                        {
                            categories.filter(c => c.products.length !== 0)?.slice(0, more ? categories?.length : 3)?.map((category, index) => {
                                return (
                                    <div key={index} onClick={() => selectCategoryHandler(category)}
                                         className="shadow border-2 cursor-pointer hover:shadow-2xl rounded p-2 hover:bg-indigo-700 hover:bg-opacity-20 hover:border-2 hover:border-indigo-700 transition-all duration-300">
                                        <div className="h-32 w-32 md:w-48 md:h-48">
                                            <img src={cloudinaryUrlGenerator(category?.image, 300)} alt=""
                                                 className="w-full h-full object-cover object-center"/>
                                        </div>
                                        <p className="py-1 font-open-sans text-center uppercase font-semibold text-indigo-700">{category.name}</p>
                                    </div>
                                )
                            })
                        }





                    </div>

                    {
                        categories.filter(c => c.products.length !== 0).length > 3 && !more && (
                            <div className="flex justify-center py-5 items-center">
                                <p onClick={() => setMore(true)} className="text-center text-indigo-700 font-bold text-lg font-open-sans cursor-pointer">
                                    More Categories
                                </p>
                            </div>
                        )
                    }


                    <div className={'mx-5 md:w-1/2 md:mx-auto fixed bottom-10'}>
                        <button
                            onClick={() => dispatch(changeRequest({
                                change: true,
                                step: 2,
                                className: 'page'
                            }))}
                            className="px-3 py-1 text-sm font-bold rounded shadow bg-gray-600 text-white uppercase font-open-sans focus:outline-none">
                            Back
                        </button>

                    </div>

                </div>


            </div>
        </div>
    );
};


export default SubCategorySelectorStep3
