import {useState} from "react";
import {useDispatch, useSelector} from "react-redux";
import {shopContentsSelector, preCartAction} from "../../../store/slices/shopContentsSlice";
import { opanCartModal } from "../../../store/slices/utilsSlice";
import {changeRequest} from "../../../store/slices/stepSlice";
import cloudinaryUrlGenerator from "../../../helpers/cloudinaryUrlGenerator";

const SubFoodItemVariantSelectorStep5 = () => {
    const dispatch = useDispatch();
    const {selectParent : variants, cart} = useSelector(shopContentsSelector)

    const [more, setMore] = useState(false);
    const openCartModalTrigger = product => {
        dispatch(preCartAction(product))
        dispatch(opanCartModal())
    }

    return (
        <div className="md:pt-36 pt-36">
            <div className="mx-5 md:w-2/3 md:mx-auto mb-16">
                <div className="inline">
                    <h2 className="font-open-sans text-lg text-center md:text-left font-bold">
                        Which option of pizza would you like to order?
                    </h2>
                    <p className="text-xs py-2 font-open-sans text-gray-800 text-center md:text-left">Showing available
                        variants of pizza.</p>

                    <div
                        className={`flex flex-wrap ${more ? 'justify-evenly' : 'md:justify-start justify-evenly'} gap-4 mt-5`}>
                        {
                            variants?.slice(0, more ? variants?.length : 3)?.map((variant, index) => {
                                return (
                                    <div key={variant.id}
                                         className="w-36 md:w-52 shadow relative overflow-hidden border-2 cursor-pointer hover:shadow-2xl rounded p-2 hover:bg-indigo-700 hover:bg-opacity-20 hover:border-2 hover:border-indigo-700 transition-all duration-300">
                                        <span
                                            className="absolute text-sm right-2 bg-yellow-500 font-open-sans font-bold tracking-wider px-2 bottom-28">{variant.price}$</span>
                                        <div className="h-32 w-32 md:w-48 md:h-48">
                                            <img src={cloudinaryUrlGenerator(variant.product_images[0]?.source)} alt=""
                                                 className="w-full h-full object-cover object-center"/>
                                        </div>
                                        <p className="py-1 font-open-sans text-center uppercase font-semibold text-indigo-700">{variant.name}</p>
                                        <p className="text-center py-2">
                                            {
                                                cart.findIndex(elem => elem.id === variant.id) === -1 ? (
                                                    <button
                                                        onClick={() => openCartModalTrigger(variant)}
                                                        className="py-1 px-3 rounded shadow font-open-sans text-xs uppercase text-white bg-yellow-500 focus:outline-none hover:shadow-xl transition-all duration-300 hover:bg-yellow-600">
                                                        ADD TO CART
                                                    </button>
                                                ): (
                                                    <button

                                                        className="py-1 px-3 rounded shadow font-open-sans text-xs uppercase text-white bg-gray-700 focus:outline-none hover:shadow-xl transition-all duration-300">
                                                        Already added!
                                                    </button>
                                                )
                                            }

                                        </p>
                                    </div>
                                )
                            })
                        }


                    </div>

                    {
                        variants.length > 3 && !more && (
                            <div className="flex justify-center py-5 items-center">
                                <p onClick={() => setMore(true)}
                                   className="text-center text-indigo-700 font-bold text-lg font-open-sans cursor-pointer">
                                    More Variants
                                </p>
                            </div>
                        )
                    }


                    <div className={'mx-5 md:w-1/2 md:mx-auto fixed bottom-10'}>
                        <button
                            onClick={() => dispatch(changeRequest({
                                change: true,
                                step: 4,
                                className: 'page'
                            }))}
                            className="px-3 py-1 text-sm font-bold rounded shadow bg-gray-600 text-white uppercase font-open-sans focus:outline-none">
                            Back
                        </button>

                    </div>
                </div>


            </div>
        </div>
    )
}


export default SubFoodItemVariantSelectorStep5
