const DateSelectorCard = ({ onSelect, date}) => {
    return (
        <div onClick={() => onSelect(date)} className={`rounded  shadow-lg px-4 py-4 cursor-pointer transition-all duration-300 hover:bg-indigo-700 hover:bg-opacity-20 hover:border-2 hover:border-indigo-700 bg-white border-2`}>
            <div className="grid grid-cols-2">
                <div
                    className="flex justify-center items-center text-5xl font-open-sans font-bold text-indigo-700">
                    {
                        date.toDateString().split(' ')[2]
                    }
                </div>
                <div className="p-2">
                    <p className={'font-open-sans text-sm text-indigo-700'}>
                        {
                            date.toDateString().split(' ')[0]
                        }
                    </p>
                    <p className={'font-open-sans font-semibold text-indigo-700'}>
                        {
                            date.toDateString().split(' ')[1]
                        }
                    </p>
                </div>
            </div>
        </div>
    )
}


export default DateSelectorCard
