import {changeRequest} from "../../../store/slices/stepSlice";
import {useDispatch, useSelector} from "react-redux";
import {changeOrderValues, orderValueSelector} from "../../../store/slices/OrderDetailsSlice";

const SubGiftOrNotStep7 = () => {
    const dispatch = useDispatch();
    const {isGift} = useSelector(orderValueSelector)

    const nextPageHandler = () => {
        dispatch(changeRequest({
            change: true,
            step: isGift ? 8 : 9,
            className: 'rpage'
        }))
    }

    return (
        <div className="md:pt-36 pt-36">
            <div className="mx-5 md:w-1/2 md:mx-auto mb-16 px-5">
                <div className="inline">

                    <div className="py-2 mt-8">
                        <p className="font-bold font-open-sans text-gray-600">Is this for Gift someone?</p>
                        <p className="font-light font-open-sans text-xs text-yellow-800 pb-4">* If yes, then you have to give him/her detail for delivery and delivery charge included!</p>
                        <p onClick={() => dispatch(changeOrderValues({name: 'isGift', value: true}))} className={'flex items-center font-open-sans text-gray-500 font-bold py-2 cursor-pointer'}>
                            <input onChange={(e) => dispatch(changeOrderValues({name: 'isGift', value: true}))} className={'mr-2 transition-all duration-300'} type="radio" checked={isGift} /> Yes
                        </p>

                        <p onClick={() =>dispatch(changeOrderValues({name: 'isGift', value: false}))} className={'flex items-center font-open-sans text-gray-500 font-bold py-2 cursor-pointer'}>
                            <input onChange={(e) => dispatch(changeOrderValues({name: 'isGift', value: false}))} className={'mr-2 transition-all duration-300'} type="radio" checked={!isGift} /> No
                        </p>

                    </div>



                    <div className={'mt-10  flex justify-between'}>
                        <button
                            onClick={() => dispatch(changeRequest({
                                change: true,
                                step: 6,
                                className: 'page'
                            }))}
                            className="px-3 py-1 text-sm font-bold rounded shadow bg-gray-600 text-white uppercase font-open-sans focus:outline-none">
                            Back
                        </button>

                        <button
                            onClick={nextPageHandler}
                            className="px-3 py-1 text-sm font-bold rounded shadow bg-indigo-600 text-white uppercase font-open-sans focus:outline-none">
                            Next
                        </button>

                    </div>




                </div>


            </div>
        </div>
    );
}

export default SubGiftOrNotStep7
