import {changeRequest} from "../../../store/slices/stepSlice";
import {useDispatch, useSelector} from "react-redux";
import {shopContentsSelector} from "../../../store/slices/shopContentsSlice";
import {orderValueSelector, changeOrderValues} from "../../../store/slices/OrderDetailsSlice";
import {toast} from "react-hot-toast";

const SubDeliveryOrPickupStep9 = () => {
    const dispatch = useDispatch();
    const {fulfillmentType, deliveryOptionId, pickupOptionId, isGift} = useSelector(orderValueSelector)
    const {fulfilment} = useSelector(shopContentsSelector)


    const nextPageHandler = () => {
        if(fulfillmentType === 'delivery'){
            if(deliveryOptionId){
                dispatch(changeRequest({
                    change: true,
                    step: 10,
                    className: 'rpage'
                }))
                return
            }else{
                toast.error('Please select a delivery option!')
                return
            }
        }

        if(fulfillmentType === 'pickup'){
            if(pickupOptionId){
                dispatch(changeRequest({
                    change: true,
                    step: 11,
                    className: 'rpage'
                }))
                return
            }else{
                toast.error('Please select a pickup option!')
                return
            }
        }

        toast.error('Please select a delivery option!')

    }

    return (
        <div className="md:pt-36 pt-36">
            <div className="mx-5 md:w-1/2 md:mx-auto mb-16 px-5">
                <div className="inline">

                    <div className="py-2 mt-8">
                        <p className="font-bold font-open-sans text-gray-600 mb-6">How would you like to get items?</p>
                        {
                            fulfilment?.deliveryOptions.length ? (
                                <>

                                    <p onClick={() => dispatch(changeOrderValues({
                                        name: 'fulfillmentType',
                                        value: 'delivery'
                                    }))}
                                       className={'flex items-center font-open-sans text-gray-500 font-bold py-2 cursor-pointer'}>
                                        <input onChange={(e) => dispatch(changeOrderValues({
                                            name: 'fulfillmentType',
                                            value: 'delivery'
                                        }))} className={'mr-2 transition-all duration-300'} type="radio"
                                               checked={fulfillmentType === 'delivery'}/> Home Delivery
                                    </p>
                                    {
                                        fulfillmentType === 'delivery' && (
                                            <ul>
                                                {
                                                    fulfilment.deliveryOptions.map(option => {
                                                        return (
                                                            <li
                                                                onClick={() => dispatch(changeOrderValues({name: 'deliveryOptionId', value: option.id}))}
                                                                className={`transition-all duration-300 flex px-4 my-1 py-1 flex-wrap rounded ${deliveryOptionId === option.id ?'bg-gray-400' : 'bg-gray-200'} shadow cursor-pointer`}>
                                                                <div
                                                                    className="flex-1 text-gray-800 font-bold">{option.title}</div>
                                                                <div
                                                                    className="w-3 mr-2 font-bold text-red-600">${parseFloat(option.price)}</div>
                                                                <div
                                                                    className="w-full text-xs">{option.description}</div>


                                                            </li>
                                                        )
                                                    })
                                                }
                                            </ul>
                                        )
                                    }


                                </>
                            ) : ""
                        }

                        {
                            fulfilment?.pickupOptions.length ? (
                                <>
                                    <p onClick={() => dispatch(changeOrderValues({
                                        name: 'fulfillmentType',
                                        value: 'pickup'
                                    }))}
                                       className={'flex items-center font-open-sans text-gray-500 font-bold py-2 cursor-pointer'}>
                                        <input onChange={(e) => dispatch(changeOrderValues({
                                            name: 'fulfillmentType',
                                            value: 'pickup'
                                        }))} className={'mr-2 transition-all duration-300'} type="radio"
                                               checked={fulfillmentType === 'pickup'}/> Pickup From Store
                                    </p>

                                    {
                                        fulfillmentType === 'pickup' && (
                                            <ul>
                                                {
                                                    fulfilment.pickupOptions.map(option => {
                                                        return (
                                                            <li
                                                                onClick={() => dispatch(changeOrderValues({name: 'pickupOptionId', value: option.id}))}
                                                                className={`transition-all duration-300 flex px-4 my-1 py-1 flex-wrap rounded ${pickupOptionId === option.id ?'bg-gray-400' : 'bg-gray-200'} shadow cursor-pointer`}>
                                                                <div
                                                                    className="flex-1 text-gray-800 font-bold">{option.title}</div>
                                                                <div
                                                                    className="w-3 mr-2 font-bold text-red-600">${parseFloat(option.price)}</div>
                                                                <div
                                                                    className="w-full text-xs">{option.description}</div>


                                                            </li>
                                                        )
                                                    })
                                                }
                                            </ul>
                                        )
                                    }
                                </>
                            ) : ''
                        }


                    </div>

                    <div className={'mt-10  flex justify-between'}>
                        <button
                            onClick={() => dispatch(changeRequest({
                                change: true,
                                step: isGift ? 8 : 7,
                                className: 'page'
                            }))}
                            className="px-3 py-1 text-sm font-bold rounded shadow bg-gray-600 text-white uppercase font-open-sans focus:outline-none">
                            Back
                        </button>

                        <button
                            onClick={nextPageHandler}
                            className="px-3 py-1 text-sm font-bold rounded shadow bg-indigo-600 text-white uppercase font-open-sans focus:outline-none">
                            Next
                        </button>

                    </div>


                </div>


            </div>
        </div>
    );
}

export default SubDeliveryOrPickupStep9
