import { createSlice } from "@reduxjs/toolkit";

const OrderDetailsSlice = createSlice({
    name: 'order',
    initialState: {
        customerName: '',
        customerPhone: '',
        customerEmail: '',
        isGift: false,
        giftReceiverName: '',
        giftReceiverEmail: '',
        giftReceiverPhone: '',
        fulfillmentType: '',
        deliveryOptionId: '',
        pickupOptionId: '',
        deliveryAddress: '',
    },
    reducers: {
        changeOrderValues: (state, {payload}) => {
            if(payload.name === 'isGift' && payload.value === false){
                state.isGift = false;
                state.giftReceiverEmail = '';
                state.giftReceiverName = '';
                state.giftReceiverPhone = '';
                return
            }
            if(payload.name === 'fulfillmentType' && payload.value === 'delivery'){
                state.fulfillmentType = 'delivery'
                state.pickupOptionId = ''
                return
            }
            if(payload.name === 'fulfillmentType' && payload.value === 'pickup'){
                state.fulfillmentType = 'pickup'
                state.deliveryAddress = ''
                state.deliveryOptionId = ''
                return
            }

            state[payload.name] = payload.value


        }
    }
})

export const orderDetailsReducer = OrderDetailsSlice.reducer
export const changeOrderValues = OrderDetailsSlice.actions.changeOrderValues
export const orderValueSelector = state => state.order