import {useState} from "react";
import {useDispatch, useSelector} from "react-redux";
import { shopContentsSelector, selectCategoryAction, selectDayForOrderAction} from "../../../store/slices/shopContentsSlice";
import {changeRequest} from "../../../store/slices/stepSlice";
import {FaCalendarDay} from 'react-icons/fa';
import DateSelectorCard from "../../../components/cards/DateSelectorCard";
import CalenderModal from "../../../components/modal/CalenderModal";

const SubOrderingDateStep2 = () => {

    const dispatch = useDispatch();

    const shopContents = useSelector(shopContentsSelector)



    const [openModal, setOpenModal] = useState(false);

    const handleSelectDay = (date) => {
        dispatch(selectDayForOrderAction(date.toDateString()))

        setOpenModal(false);
        shopContents.categories.length ? dispatch(selectCategoryAction({}))  :  dispatch(selectCategoryAction(shopContents.uncategorized))


        dispatch(changeRequest(
            {
                change: true,
                className: 'rpage',
                step: shopContents.categories.length ? 3 : 4
            }
        ))

    }

    return (
        <div className="md:pt-48 pt-36">
            <CalenderModal
                onRequestClose={() => setOpenModal(false)}
                isOpen={openModal}
                onDaySelect={handleSelectDay}
            />
            <div className="mx-5 md:w-1/2 md:mx-auto">
                <div className="inline">
                    <h2 className="font-open-sans text-lg text-center md:text-left font-bold">
                        When would you like to order for?
                    </h2>
                    <p className="text-xs py-2 font-open-sans text-gray-800 text-center md:text-left">Showing next
                        available dates.</p>

                    <div className="flex  flex-wrap justify-evenly md:justify-start gap-4 mt-5">
                        {
                            shopContents?.availabilities?.slice(0, 4)?.map((day, index) => {

                                return (
                                    <DateSelectorCard
                                        key={index}
                                        onSelect={handleSelectDay}
                                        date={new Date(day.date)}
                                    />
                                )
                            })
                        }

                    </div>

                    <div className="flex justify-center py-5 items-center">
                        <p onClick={() => setOpenModal(true)}
                           className="text-center text-indigo-700 font-bold text-lg font-open-sans cursor-pointer">
                            <FaCalendarDay className={'inline mr-2'}/>
                            More dates.
                        </p>
                    </div>

                    <div className={'mx-5 md:w-1/2 md:mx-auto fixed bottom-10'}>
                        <button
                            onClick={() => dispatch(changeRequest({
                                change: true,
                                step: 1,
                                className: 'page'
                            }))}
                            className="px-3 py-1 text-sm font-bold rounded shadow bg-gray-600 text-white uppercase font-open-sans focus:outline-none">
                            Back
                        </button>

                    </div>





                </div>

            </div>
        </div>
    );
};


export default SubOrderingDateStep2
