import React, {useState, useEffect} from "react";
import { useDispatch, useSelector } from "react-redux";
import {changeRequest, currentStepSelector } from "../../store/slices/stepSlice";
import {shopContentsSelector } from "../../store/slices/shopContentsSlice";

import {FaTimes, FaShoppingCart } from "react-icons/fa";
import CartItem from "./CartItem";

const Cart = () => {
    const dispatch = useDispatch();
    const step = useSelector(currentStepSelector);
    const {cart} = useSelector(shopContentsSelector)

    const [open, setOpen] = useState(false);

    useEffect(() => {
        setOpen(false)
    }, [step])

    useEffect(() => {
        if(cart.length > 0){
            setOpen(true)
        }

    }, [cart.length])

    const proceed = () => {

        dispatch(
            changeRequest(
                {
                    change: true,
                    step: 6,
                    className: 'rpage'
                }
            )
        );
    }

    const totalPrice = () => {
        let price = 0
        cart.forEach(c => price += parseFloat(c.price) * c.qty)
        return price
    }


    return (
        <>
            {
                step < 6 && (
                    <div onClick={() => setOpen(true)} className="fixed h-8 w-8 rounded-full shadow-2xl border-2 cursor-pointer right-10 top-10 z-50 bg-black flex justify-center items-center">
                        <div className="absolute text-xs flex justify-center items-center -right-1 -top-1 w-4 h-4 rounded-full bg-red-500 tracking-tight font-open-sans text-white font-medium">
                            {cart.length}
                        </div>
                        <FaShoppingCart
                            color={'white'}
                        />
                    </div>
                )
            }


            {
                open && (
                    <div onClick={() => setOpen(false)} className="fixed h-screen w-screen -left-0 -top-0 bg-gray-600 bg-opacity-40 z-50">

                    </div>
                )
            }



            <div
                className={`fixed transition-all duration-300 pb-10 ${open ? '-bottom-0' : '-bottom-full'} bg-white left-1/2 transform -translate-x-1/2 rounded-tl-xl rounded-tr-xl border-2 shadow-xl w-full md:w-1/2 max-h-1/2 overflow-full-scroller-hidden z-50`}>
                <div onClick={() => setOpen(false)}
                     className="absolute right-5 top-3 bg-black w-6 h-6 rounded-full shadow cursor-pointer flex items-center justify-center">
                    <FaTimes color={'white'}/>
                </div>

                <div
                     className="absolute left-5 top-3 inline p-1 h-6  shadow cursor-pointer flex items-center justify-center">
                    $
                    {
                         totalPrice()
                    }
                </div>
                <h2 className="pt-5 pb-2 mx-10 text-open-sans font-bold text-center text-gray-800 border-b">My Orders</h2>
                {
                    cart.length === 0 ? (
                        <p className="my-10 mx-10 py-2 text-yellow-500  font-open-sans text-center font-bold text-lg">
                            Nothing Added Here!
                        </p>
                    ) : (
                        cart.map(item => {
                            return <CartItem key={item.id} item={item} />
                        })
                    )
                }


                <div className="my-2 mx-4">
                    {
                        cart.length ? (
                            <button onClick={proceed} className="w-full rounded bg-indigo-500 py-2 font-open-sans text-xl uppercase focus:outline-none border-none shadow text-white">
                                Proceed To Checkout
                            </button>
                        ): (
                            <button className="w-full rounded bg-gray-500 py-2 font-open-sans text-xl uppercase focus:outline-none border-none shadow text-white">
                                No item to proceed!
                            </button>
                        )
                    }

                </div>


            </div>
        </>
    )
}

export default Cart
