import { createSlice } from "@reduxjs/toolkit";

const utilsSlice = createSlice({
    name: 'utils',
    initialState: {
        addToCartModal: {
            open: false,
            insideCart: {

            }
        }
    },
    reducers: {
        opanCartModal: state => {
            state.addToCartModal.open = true;
        },
        closeCartModal: state => {
            state.addToCartModal.open = false;
        }
    }
})


export default utilsSlice.reducer
export const {
    opanCartModal,
    closeCartModal
} = utilsSlice.actions

export const cartModalSelector = state => state.utils.addToCartModal
