import {changeRequest} from "../../../store/slices/stepSlice";
import { useDispatch, useSelector } from "react-redux";
import {orderValueSelector, changeOrderValues} from "../../../store/slices/OrderDetailsSlice";
import {toast} from "react-hot-toast";

const SubGiftPickerNameAndPhoneStep8 = () => {
    const dispatch = useDispatch();
    const { giftReceiverName, giftReceiverEmail, giftReceiverPhone} = useSelector(orderValueSelector)


    const nextPageHandler = () => {
        if(giftReceiverEmail && giftReceiverName && giftReceiverPhone){
            dispatch(changeRequest({
                change: true,
                step: 9,
                className: 'rpage'
            }))
        }else{
            toast.error('Please fill all fields!')
        }

    }
    const changeValue = e => {
        dispatch(changeOrderValues({name: e.target.name, value: e.target.value}))
    }

    return (
        <div className="md:pt-36 pt-36">
            <div className="mx-5 md:w-1/2 md:mx-auto mb-16 px-5">
                <div className="inline">
                    <div className="py-2">
                        <p className="font-bold font-open-sans text-gray-600 pb-4">Recipient's Name?</p>
                        <input
                            value={giftReceiverName}
                            name={'giftReceiverName'}
                            onChange={changeValue}
                            type="text"
                            className="focus:outline-none border border-gray-600 shadow w-full rounded px-2 py-2 font-open-sans text-gray-600"
                            placeholder={'Enter recipient\'s name here'}

                        />
                    </div>


                    <div className="py-2 mt-2">
                        <p className="font-bold font-open-sans text-gray-600">Recipient's phone number?</p>
                        <p className="font-light font-open-sans text-xs text-gray-500 pb-2">No spam, For order purpose
                            only!</p>
                        <input
                            name={'giftReceiverPhone'}
                            value={giftReceiverPhone}
                            onChange={changeValue}
                            type="text"
                            className="focus:outline-none border border-gray-600 shadow w-full rounded px-2 py-2 font-open-sans text-gray-600"
                            placeholder={'Enter your phone number here'}

                        />
                    </div>

                    <div className="py-2 mt-2">
                        <p className="font-bold font-open-sans text-gray-600">Recipient's Email Address?</p>
                        <p className="font-light font-open-sans text-xs text-gray-500 pb-2">No spam, For order purpose
                            only!</p>
                        <input
                            type="email"
                            value={giftReceiverEmail}
                            name={'giftReceiverEmail'}
                            onChange={changeValue}
                            className="focus:outline-none border border-gray-600 shadow w-full rounded px-2 py-2 font-open-sans text-gray-600"
                            placeholder={'Enter your email address'}

                        />
                    </div>



                    <div className={'mt-10  flex justify-between'}>
                        <button
                            onClick={() => dispatch(changeRequest({
                                change: true,
                                step: 7,
                                className: 'page'
                            }))}
                            className="px-3 py-1 text-sm font-bold rounded shadow bg-gray-600 text-white uppercase font-open-sans focus:outline-none">
                            Back
                        </button>

                        <button
                            onClick={nextPageHandler}
                            className="px-3 py-1 text-sm font-bold rounded shadow bg-indigo-600 text-white uppercase font-open-sans focus:outline-none">
                            Next
                        </button>

                    </div>



                </div>


            </div>
        </div>
    );
}

export default SubGiftPickerNameAndPhoneStep8
