import React from "react";
import {useSelector, useDispatch} from "react-redux";
import { currentStepSelector, currentRequestSelector, changeRequest, changeStep} from "../../store/slices/stepSlice";
import { useEffect} from "react";
import Cart from '../../components/Cart'
import { SwitchTransition, CSSTransition} from "react-transition-group";
import SubOrderingDateStep2 from "./SubOrderingDateStep2";
import SubCategorySelectorStep3 from "./SubCategorySelectorStep3";
import SubFoodItemSelectorStep4 from "./SubFoodItemSelectorStep4";
import SubFoodItemVariantSelectorStep5 from "./SubFoodItemVarientSelectorStep5";
import HomeLayout from "../../layouts/HomeLayout";
import WelcomePage from "./WelcomePage";
import SubCustomerNameAndPhoneStep6 from "./SubCustomerNameAndPhoneStep6";
import SubGiftOrNotStep7 from "./SubGiftOrNotStep7";
import SubGiftPickerNameAndPhoneStep8 from "./SubGiftPickerNameAndPhoneStep8";
import SubDeliveryOrPickupStep9 from "./SubDeliveryOrPickupStep9";
import SubAddressFieldStep10 from "./SubAddressFieldStep10";
import SubPaymentMethodSelectionStep11 from "./SubPaymentMethodSelectionStep11";

// import modal
import AddToCartModal from "../../components/modal/AddToCartModal";


const Homepage = () => {
    const dispatch = useDispatch();
    const step = useSelector(currentStepSelector);
    const request = useSelector(currentRequestSelector);






    useEffect(() => {
        if(request.change){
            dispatch(changeStep(request.step))
            dispatch(
            changeRequest({
                ...request,
                change: false
            }))
        }
    }, [request, dispatch]);





    const stepper = step => {
        switch (step) {
            case 1:
                return <WelcomePage />;
            case 2:
                return <SubOrderingDateStep2 />;
            case 3:
                return <SubCategorySelectorStep3 />;
            case 4:
                return <SubFoodItemSelectorStep4 />;
            case 5:
                return <SubFoodItemVariantSelectorStep5 />
            case 6:
                return <SubCustomerNameAndPhoneStep6 />
            case 7:
                return <SubGiftOrNotStep7 />
            case 8:
                return <SubGiftPickerNameAndPhoneStep8 />
            case 9:
                return <SubDeliveryOrPickupStep9 />
            case 10:
                return <SubAddressFieldStep10 />
            case 11:
                return <SubPaymentMethodSelectionStep11 />
            default:
                return <WelcomePage />;

        };
    };




    return (
        <HomeLayout>
            <SwitchTransition
                mode={'out-in'}
            >
                <CSSTransition
                    key={step}
                    appear={true}
                    classNames={request.className}
                    timeout={300}
                >
                    <div className="overflow-full-scroller-hidden h-screen w-screen;">
                        {
                            stepper(step)
                        }
                    </div>

                </CSSTransition>
            </SwitchTransition>
            <Cart />
            <AddToCartModal />


        </HomeLayout>
    );
};

export default Homepage;
