const cloudinaryUrlGenerator = (publicId, height, width) => {
    const appid = 'dev-Algosolver2021'
    if(publicId){
        return `https://res.cloudinary.com/${appid}/image/upload${height || width ? `/${height ? `h_${height},`: ''}${width ? `w_${width}` : ''}` : ''}/v1/${publicId}`
    }else{
        return `https://res.cloudinary.com/${appid}/image/upload${height || width ? `/${height ? `h_${height},`: ''}${width ? `w_${width}` : ''}` : ''}/v1/kortex/products/download_2_o6jlcg.png`
    }




}

export default cloudinaryUrlGenerator
