import {useState} from "react";
import {useDispatch, useSelector} from "react-redux";
import {changeRequest} from "../../../store/slices/stepSlice";
import {shopContentsSelector, selectParentAction, preCartAction} from "../../../store/slices/shopContentsSlice";
import cloudinaryUrlGenerator from "../../../helpers/cloudinaryUrlGenerator";
import {opanCartModal} from "../../../store/slices/utilsSlice";

const SubFoodItemSelectorStep4 = () => {
    const dispatch = useDispatch();
    const {selectedCategory: items, categories, cart} = useSelector(shopContentsSelector);

    const [more, setMore] = useState(false);


    const handleClickOnItem = (product) => {
        if (product.hasVariant) {
            dispatch(selectParentAction(product.sub_products))
            dispatch(changeRequest(
                {
                    change: true,
                    step: 5,
                    className: 'rpage'
                }
            ))
        }


    }

    const openCartModalTrigger = product => {
        dispatch(preCartAction(product))
        dispatch(opanCartModal())
    }


    return (
        <div className="md:pt-36 pt-36">
            <div className="mx-5 md:w-2/3 md:mx-auto mb-16">
                <div className="inline">
                    <h2 className="font-open-sans text-lg text-center md:text-left font-bold">
                        What would you like to order?
                    </h2>
                    <p className="text-xs py-2 font-open-sans text-gray-800 text-center md:text-left">Showing available
                        items.</p>

                    <div
                        className={`flex flex-wrap ${more ? 'justify-evenly' : 'md:justify-start justify-evenly'} gap-4 mt-5`}>
                        {
                            items.filter(i => {
                                if (i.hasVariant) {
                                    return i.sub_products.length !== 0;
                                } else {
                                    return true
                                }
                            })?.slice(0, more ? items?.length : 3)?.map((item, index) => {
                                return (
                                    <div key={item.id}
                                         className="shadow relative overflow-hidden border-2 cursor-pointer hover:shadow-2xl rounded p-2 hover:bg-indigo-700 hover:bg-opacity-20 hover:border-2 hover:border-indigo-700 transition-all duration-300">
                                        {
                                            item.hasVariant ? '' : <span
                                                className="absolute text-sm right-2 bg-yellow-500 font-open-sans font-bold tracking-wider px-2 bottom-28">{item.price}$</span>
                                        }

                                        <div className="h-32 w-32 md:w-48 md:h-48"
                                             onClick={() => handleClickOnItem(item)}>
                                            <img src={cloudinaryUrlGenerator(item.product_images[0]?.source, 300)}
                                                 alt=""
                                                 className="w-full h-full object-cover object-center"/>
                                        </div>
                                        <p className="py-1 font-open-sans text-center font-semibold text-indigo-700">{item.name}</p>
                                        <p className="text-center py-2">
                                            {
                                                item.hasVariant ? (
                                                    <button onClick={() => handleClickOnItem(item)}
                                                            className="py-1 px-3 rounded shadow font-open-sans text-xs uppercase text-white bg-blue-500 focus:outline-none hover:shadow-xl transition-all duration-300 hover:bg-blue-600">
                                                        SHOW VARIANTS
                                                    </button>
                                                ) : (
                                                    cart.findIndex(elem => elem.id === item.id) === -1 ? (
                                                        <button
                                                            onClick={() => openCartModalTrigger(item)}
                                                            className="py-1 px-3 rounded shadow font-open-sans text-xs uppercase text-white bg-yellow-500 focus:outline-none hover:shadow-xl transition-all duration-300 hover:bg-yellow-600">
                                                            ADD TO CART
                                                        </button>
                                                    ) : (
                                                        <button

                                                            className="py-1 px-3 rounded shadow font-open-sans text-xs uppercase text-white bg-gray-700 focus:outline-none hover:shadow-xl transition-all duration-300">
                                                            Already added!
                                                        </button>
                                                    )

                                                )
                                            }


                                        </p>
                                    </div>
                                )
                            })
                        }


                    </div>

                    {
                        items.filter(i => {
                            if (i.hasVariant) {
                                return i.sub_products.length !== 0;
                            } else {
                                return true
                            }
                        }).length > 3 && !more && (
                            <div className="flex justify-center py-5 items-center">
                                <p onClick={() => setMore(true)}
                                   className="text-center text-indigo-700 font-bold text-lg font-open-sans cursor-pointer">
                                    More Items
                                </p>
                            </div>
                        )
                    }

                    <div className={'mx-5 md:w-1/2 md:mx-auto fixed bottom-10'}>
                        <button
                            onClick={() => dispatch(changeRequest({
                                change: true,
                                step: categories.length ? 3 : 2,
                                className: 'page'
                            }))}
                            className="px-3 py-1 text-sm font-bold rounded shadow bg-gray-600 text-white uppercase font-open-sans focus:outline-none">
                            Back
                        </button>

                    </div>
                </div>


            </div>
        </div>
    );
}

export default SubFoodItemSelectorStep4
