import {createSlice } from "@reduxjs/toolkit";

const ShopContentsSlice = createSlice({
    name: 'shop',
    initialState: {
        availabilities: [],
        categories: [],
        uncategorized: [],
        selectedCategory: [],
        selectedParent: [],
        selectedDate: '',
        preCart: {},
        cart: [],
        fulfilment: {
            pickupOptions: [],
            deliveryOptions: []
        }
    },
    reducers: {
        initShop: (state, {payload}) => {
            state.availabilities = payload.availabilities;
            state.categories = payload.categories;
            state.uncategorized = payload.uncategorized;
            state.fulfilment = payload.fulfillment
        },
        selectCategory: (state, {payload}) => {
            state.selectedCategory = payload
        },
        selectParent: (state, {payload}) => {
            state.selectParent = payload
        },
        selectDayForOrder: (state, {payload}) => {
            state.selectedDate = payload
        },
        insertPreCart: (state, {payload}) => {
            state.preCart = {...payload, qty: 1}
        },
        incPreCartQty: (state) => {
            state.preCart.qty = state.preCart.qty + 1
        },
        decPreCartQty: (state) => {
            state.preCart.qty = state.preCart.qty - 1
        },
        addToCart: (state, {payload}) => {
            state.cart.push(payload)
        },
        incrementQtyFromCart: (state, {payload}) => {
            state.cart = state.cart.map(item => {
                if(item.id === payload && item.stock > item.qty){
                    return {...item, qty: item.qty + 1}
                }else{
                    return {...item}
                }
            })
        },
        decQtyFromCart: (state, {payload}) => {
            state.cart = state.cart.map(item => {
                if(item.id === payload && item.qty > 1){
                    return {...item, qty: item.qty - 1}
                }else{
                    return {...item}
                }
            })
        },
        deleteItemFromCart: (state, {payload}) => {
            state.cart = state.cart.filter(item => item.id !== payload)
        }
    }
})


export const shopContentsReducer = ShopContentsSlice.reducer;
export const initShopAction = ShopContentsSlice.actions.initShop
export const selectCategoryAction = ShopContentsSlice.actions.selectCategory
export const selectParentAction = ShopContentsSlice.actions.selectParent
export const selectDayForOrderAction = ShopContentsSlice.actions.selectDayForOrder
export const preCartAction = ShopContentsSlice.actions.insertPreCart
export const incPreCartQtyAction = ShopContentsSlice.actions.incPreCartQty
export const decPreCartQtyAction = ShopContentsSlice.actions.decPreCartQty
export const addToCartAction = ShopContentsSlice.actions.addToCart
export const incCartItemQtyAction = ShopContentsSlice.actions.incrementQtyFromCart
export const decCartItemQtyAction = ShopContentsSlice.actions.decQtyFromCart
export const deleteFromCartAction = ShopContentsSlice.actions.deleteItemFromCart
export const shopContentsSelector = state => state.shop