import {changeRequest} from "../../../store/slices/stepSlice";
import { useDispatch, useSelector } from "react-redux";
import {orderValueSelector, changeOrderValues} from "../../../store/slices/OrderDetailsSlice";
import {toast} from "react-hot-toast";

const SubAddressFieldStep10 = () => {
    const dispatch = useDispatch();
    const {deliveryAddress} = useSelector(orderValueSelector)

    const nextPageHandler = () => {
        if(deliveryAddress){
            dispatch(changeRequest({
                change: true,
                step: 11,
                className: 'rpage'
            }))
        }else{
            toast.error('Please fill the address field!')
        }

    }
    const changeValue = e => {
        dispatch(changeOrderValues({name: e.target.name, value: e.target.value}))
    }
    return (
        <div className="md:pt-36 pt-36">
            <div className="mx-5 md:w-1/2 md:mx-auto mb-16 px-5">
                <div className="inline">
                    <div className="py-2 mt-8">
                        <p className="font-bold font-open-sans text-gray-600 pb-4">Delivery Address?</p>
                        <p className="font-light font-open-sans text-xs text-yellow-800 pb-4">* Your order confirmation will be sent there!</p>
                        <input
                            type="text"
                            value={deliveryAddress}
                            name={'deliveryAddress'}
                            onChange={changeValue}
                            className="focus:outline-none border border-gray-600 shadow w-full rounded px-2 py-2 font-open-sans text-gray-600"
                            placeholder={'Enter delivery address here'}

                        />
                    </div>


                    <div className={'mt-10  flex justify-between'}>
                        <button
                            onClick={() => dispatch(changeRequest({
                                change: true,
                                step: 9,
                                className: 'page'
                            }))}
                            className="px-3 py-1 text-sm font-bold rounded shadow bg-gray-600 text-white uppercase font-open-sans focus:outline-none">
                            Back
                        </button>

                        <button
                            onClick={nextPageHandler}
                            className="px-3 py-1 text-sm font-bold rounded shadow bg-indigo-600 text-white uppercase font-open-sans focus:outline-none">
                            Next
                        </button>

                    </div>




                </div>


            </div>
        </div>
    );
}

export default SubAddressFieldStep10
