import { useState } from 'react';
import {changeRequest} from "../../../store/slices/stepSlice";
import {useDispatch, useSelector} from "react-redux";
import {orderValueSelector} from "../../../store/slices/OrderDetailsSlice";

const SubPaymentMethodSelectionStep11 = () => {
    const dispatch = useDispatch();
    const [method, setMethod] = useState();
    const {fulfillmentType} = useSelector(orderValueSelector)

    const nextPageHandler = () => {
        alert('payment method '  + method)
    }

    return (
        <div className="md:pt-36 pt-36">
            <div className="mx-5 md:w-1/2 md:mx-auto mb-16 px-5">
                <div className="inline">

                    <div className="py-2 mt-8">
                        <p className="font-bold font-open-sans text-gray-600 mb-6">How would you like to get items?</p>
                        <p onClick={() => setMethod('stripe')} className={'flex items-center font-open-sans text-gray-500 font-bold py-2 cursor-pointer'}>
                            <input onChange={(e) => setMethod('stripe')} className={'mr-2 transition-all duration-300'} type="radio" checked={method === 'stripe'} /> Card Payment
                        </p>

                        <p onClick={() => setMethod('paypal')} className={'flex items-center font-open-sans text-gray-500 font-bold py-2 cursor-pointer'}>
                            <input onChange={(e) => setMethod('paypal')} className={'mr-2 transition-all duration-300'} type="radio" checked={method === 'paypal'} /> Paypal
                        </p>


                    </div>


                    <div className={'mt-10  flex justify-between items-center'}>
                        <button
                            onClick={() => dispatch(changeRequest({
                                change: true,
                                step: fulfillmentType === 'delivery' ? 10 : 9,
                                className: 'page'
                            }))}
                            className="px-3 py-1 text-sm font-bold rounded shadow bg-gray-600 text-white uppercase font-open-sans focus:outline-none">
                            Back
                        </button>

                        <button
                            onClick={nextPageHandler}
                            className="px-3 py-1 text-lg  rounded font-bold  shadow bg-red-400 text-black  uppercase font-open-sans focus:outline-none">
                            Make Payment
                        </button>

                    </div>




                </div>


            </div>
        </div>
    );
}

export default SubPaymentMethodSelectionStep11
