import {FaMinus, FaPlus, FaTrashAlt} from "react-icons/fa";
import React from "react";
import cloudinaryUrlGenerator from "../../helpers/cloudinaryUrlGenerator";
import { useDispatch } from "react-redux";
import {incCartItemQtyAction, decCartItemQtyAction, deleteFromCartAction } from "../../store/slices/shopContentsSlice";

const CartItem = ({item}) => {
    const dispatch = useDispatch();
    return (
        <div className="border-b pb-2 pt-1 px-2">
            <div className="grid grid-cols-12">
                <div className="col-span-1 flex items-center">
                    <img src={cloudinaryUrlGenerator(item.product_images[0].source)} alt="item" className="w-full h-8 md:h-12 object-cover object-center"/>
                </div>

                <div className="col-span-3 px-1">
                    <h2 className={'font-open-sans font-bold text-gray-800 pb1'}>{item.name}</h2>
                    <p className="text-gray-600 font-open-sans text-sm">${parseFloat(item.price)} * {item.qty} = ${parseFloat(item.price) * item.qty}</p>
                </div>

                <div className="col-span-1">

                </div>

                <div className="col-span-4 flex items-center justify-between border px-2 rounded">
                    <div onClick={() => dispatch(decCartItemQtyAction(item.id))} className="h-7 w-7 overflow-hidden cursor-pointer rounded-full bg-gray-600 flex justify-center items-center">
                        <FaMinus className={'inline'}  color={'white'}/>
                    </div>

                    <span className={'font-open-sans text-gray-700 font-bold text-lg'}>{item.qty}</span>

                    <div onClick={() => dispatch(incCartItemQtyAction(item.id))} className="h-7 w-7 overflow-hidden cursor-pointer rounded-full bg-gray-600 flex justify-center items-center">
                        <FaPlus className={'inline'}  color={'white'}/>
                    </div>
                </div>

                <div className="col-span-1 ">

                </div>

                <div className="col-span-1 flex items-center justify-end">
                    <FaTrashAlt
                        onClick={() => dispatch(deleteFromCartAction(item.id))}
                        color={'red'}
                        size={20}
                        className={'cursor-pointer'}
                    />
                </div>
            </div>
        </div>
    )
}

export default CartItem
