import Modal from 'react-modal'
import {useSelector} from "react-redux";
import {shopContentsSelector} from "../../../store/slices/shopContentsSlice";
import DayPicker from 'react-day-picker';
import 'react-day-picker/lib/style.css';

Modal.setAppElement('#root');


const CalenderModal = ({onDaySelect, isOpen, onRequestClose}) => {

    const shopContents = useSelector(shopContentsSelector)
    const days = shopContents.availabilities.map(e => new Date(e.date).toDateString())
    const customStyles = {
        content: {
            top: '50%',
            left: '50%',
            right: 'auto',
            bottom: 'auto',
            marginRight: '-50%',
            transform: 'translate(-50%, -50%)',
        },
    };

    return (
        <Modal
            isOpen={isOpen}
            onRequestClose={onRequestClose}
            style={customStyles}
        >
            <DayPicker
                onDayClick={(day, {disabled}) =>  !disabled && onDaySelect(day)}
                disabledDays={date => {
                    return days.findIndex(d => d === date.toDateString()) === -1;
                }}
            />

        </Modal>
    )
}

export default CalenderModal
