import {useEffect, useState} from "react";
import axios from "axios";
import {useDispatch} from "react-redux";
import {initShopAction} from "./store/slices/shopContentsSlice";
import Homepage from './pages/Homepage'
import {Toaster} from "react-hot-toast";

function App() {

    const dispatch = useDispatch();

    const [validShop, setValidShop] = useState({
        loading: true,
        valid: false
    })

    useEffect(() => {
        setValidShop({
            loading: true,
            valid: false
        })
        const slug = window.location.host.split('.')[0]

        axios.get('/v1/shop/' + slug)
            .then(res => {
                dispatch(initShopAction(res.data.data))
                setValidShop({
                    loading: false,
                    valid: true
                })
            })
            .catch(err => setValidShop({
                loading: false,
                valid: false
            }))
    }, [dispatch])


    return (
        <>
            <Toaster/>
            {
                validShop.loading ? (
                    ''
                ) : (
                    validShop.valid ? (
                        <Homepage/>
                    ) : (
                        <div className="h-screen w-screen flex justify-center items-center">
                            No shop found!
                        </div>
                    )
                )
            }

        </>

    );
}

export default App;
